@font-face {
  font-family: 'AtariSt';
  src: url('/assets/AtariST8x16SystemFont.eot'); /* IE9 Compat Modes */
  src: url('/assets/AtariST8x16SystemFont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('/assets/AtariST8x16SystemFont.woff') format('woff'), /* Modern Browsers */
  url('/assets/AtariST8x16SystemFont.woff2') format('woff2'), /* Modern Browsers */
  url('/assets/AtariST8x16SystemFont.ttf')  format('truetype'); /* Safari, Android, iOS */
}


