  url('/assets/atari-st-low-res.woff') format('woff'), /* Modern Browsers */
  url('/assets/atari-st-low-res.woff2') format('woff2'), /* Modern Browsers */
  url('/assets/atari-st-low-res.ttf')  format('truetype'); /* Safari, Android, iOS */
}


:root {
  --border-width: 2px;


}

html, body, #root {
  height: 100%;
}


#root {
  cursor: url('/assets/cursor.png') 0 0, pointer;
  font-family: AtariSt;
}

.top-bar {
  width:100%;
  background-color: #fff;
  display:flex;
  flex-direction: row;
  user-select: none;
}

body{
  background-image: url('/assets/background32.png');
  background-repeat: repeat;
}

.desktop-background {
  height:100%;

}

.desktop-background .menu-bar {
  border-bottom: 2px solid #000;
}
.desktop-background .menu-bar div:hover {
  background-color: #000;
  color:#fff;
}
.menu-bar div {
  margin: 0 10px;
}

.files-window {
  background-color: #fff;
  position: absolute;
  top:150px;
  left:100px;
  border:var(--border-width) solid #000;
}

.files-window .top-bar {
  border-bottom: var(--border-width) solid #000;
}
.files-window .info-bar {
  padding: 0 1rem;
  border-bottom: var(--border-width) solid #000;
}
.top-bar div.title{
  margin: 0 5rem;
  flex:1;
}

.scroll-bars {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


.scroll-bars .content{
  flex:1;
  padding:1rem;
}
.scroll-bars .vertical-scroll{
  width:1rem;
  border-left: var(--border-width) solid #000;

}
.scroll-bars .horizontal-scroll{
  border-top: var(--border-width) solid #000;
  height:1rem
}
.scroll-bars .square{
  border-left: var(--border-width) solid #000;
  border-top: var(--border-width) solid #000;
  width:1rem;
  height:1rem
}
.scroll-bars div:nth-child(3n){
  flex-basis: calc(100% - 1.13rem);
}



.file-icon,
.floppy-icon {
  width: 90px;
  height: 80px;
  background-size: 64px;
  background-position: center top;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.file-icon{
  background-image: url("/assets/file.png");
  background-repeat: no-repeat;
}

.file-icon .download-link {
  user-select: none;
}

.floppy-icon {
  position: absolute;
}

.floppy-icon div{
  white-space: nowrap;
  background-color: #fff;
}

.floppy-a {
  top:20px;
  left:20px;
  background-image: url("/assets/floppyA.png");
  background-repeat: no-repeat;
}

.floppy-b {
  top:20px;
  left:220px;
  background-image: url("/assets/floppyB.png");
  background-repeat: no-repeat;
}

.text-window {
  position: absolute;
  top:100px;
  left:450px;
  border:var(--border-width) solid #000;
  background-color: #fff;
  max-width: 400px;
}
.text-window .top-bar {
  align-items: center;
  border-bottom:var(--border-width) solid #000;

}
.text-window .top-bar .close{
  height: 18px;
  width: 17px;
  background-image: url("/assets/close-icon.png");
  background-repeat: no-repeat;
  background-position: center center;
}

.text-window .top-bar .close:nth-child(1) {
  border-right:var(--border-width) solid #000;

}

.text-window .top-bar .close:nth-child(3) {
  border-left:var(--border-width) solid #000;
}

.text-window .content {
  padding:1rem;
}